import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';

export const environment = {
  spinner: NgxSpinnerService,
  production: false,
  Url: ' http://134.209.147.250:3000/api/admin',
  IconUrl: ' http://134.209.147.250:3000/img',
  mapApiKey: 'AIzaSyDqdPKQqGBAi72_3lh-ewMghl0jzRpkiS8',

  swalalert(type, msg) {
    console.log(type, msg)
    if (type === 'success') {
      return new Promise(resolve => {
        Swal.fire({
          type: 'success',
          title: 'Success',
          text: msg,
          showConfirmButton: false,
          timer: 1500
        }).then(async (result) => {
          resolve(true);
        });
      });
    } else if (type === 'delete') {
      return new Promise(resolve => {
        Swal.fire({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, ' + msg + ' it!'
        }).then(async (result) => {
          if (result.value) {
            Swal.fire({
              title: msg + '!',
              text: 'Your file has been ' + msg + '.',
              type: 'success',
              showConfirmButton: false,
              timer: 1000
            }).then(async (result) => {
              resolve(true);
            })
          }
        })
      });
    } else if (type === 'nodata' || type === 'underconst') {
      Swal.fire({
        title: '<strong>' + msg + '</strong>',
        type: 'warning',
        html: 'You can use <b>' + msg + '</b>',
        showCloseButton: true,
        showCancelButton: false,
        focusConfirm: false,
        confirmButtonText:'Okay'
      })
    }
    else {
      Swal.fire({
        type: type,
        title: 'OOPS!',
        text: msg,
        showConfirmButton: true,
      });
    }
  }
};